//kalyanchik project less styles ver. 0.9
@import "less/bootstrap.less";

//
// ****************************** header
//
//

body{
  background: url(../../image/body-bg.png) repeat;
}
header{
  background: url("../../image/bg-header-mob.jpg") center center;
  background-size: cover;

  .sm({
    .height(180);
  });

  .md({
    background: url("../../image/header-bg.jpg") center center;
    background-size: cover;
  });

}

#top{
  background: rgba(51,51,51,.75);

  &.active-in{
    background: url("../../image/footer-bg.png") repeat;

    .container{
      padding: 0;
    }
    .btn-link{
      padding: 10px 25px;
    }

    #nav-btn{
      color: @lighten-color;
    }

    #common-nav,
    .btn-div{
      border-top: 1px solid @border-dark-nav-color;
    }
    .btn-div{
      border-bottom: 1px solid #000;
    }

  }
}


#top-links{
  .sm({
  .btn-link{
    padding-right: 0;
  }
  });

  ul{
    .size-reset;
    &.list-inline{
      float: left;
    }
  }

  .dropdown{
    > a{
      span:first-child{
        padding-right: 10px;
      }
    }
  }

}

#top-links,
#common-nav{

  line-height: 0;

}

.btn-div{
  border: none;
}

nav{
  .fa{
    font-size: 21px;
  }
  .fa-angle-down{
    font-size: 18px;
  }
}

.nav{
  .size-reset;

  > li {
    position: relative;
    display: block;

    > a {
      position: relative;
      display: block;
      padding: @nav-link-padding;
      font-size: @nav-font-size;
      line-height: @font-size-base;
      color: @nav-color;
      .transition(all .15s ease-in);

      &:hover {
        color: @lighten-color;
      }

      &:hover,
      &:focus {
        text-decoration: none;
      }
    }
  }

  .sm({

  >li {

    > a {
      padding: 15px 0 15px 15px;
      font-size: @font-size-base;
      line-height: @font-size-base;

      &:after {
        content: "•";
        padding-left: 15px;
        color: @nav-color;
      }

    }

    &:first-child a {
      padding-left: 0;
    }

    &:last-child {
      a:after {
        content: "";
      }
    }

  }

  });

}

.navbar-toggle-top-links{
  border: none;
}


#logo{
  margin: 40px 0;

  .sm({
    margin: 20px 0;
  });

  img{
    margin: 0 auto;
  }

}
.header-contacts{
  z-index: 1;

  ul{
    .size-reset;

    li{
      list-style: none;

      &:last-child{
        cursor: default;
      }

      &:last-child,
      a{
        display: block;
        color: @main-color;
        padding: (@grid-gutter-width / 2) 0;
        font-size: @contact-mob-font-size;
        line-height: @font-size-base;
        text-align: center;

        @media screen and (max-width: 380px){
          font-size: @font-size-base - 1;
        }

        &:hover{
          color: @lighten-color;
        }

        &> span{
          .bold;
        }
      }

      .max-sm({
      &:first-child{
        a{
          padding-top: 40px;
        }
      }
      &:nth-of-type(3){
        a{
          padding-bottom: 35px;
        }
      }
      });


      .max-sm({
        &.header-address{
          display: none;
        }
      });
    }


  }

  .sm({
    padding-top: 10px;
    margin: 0;

  ul{

    li{
      &:last-child{
        padding: 10px 0 20px 0;
        line-height: @contact-mob-font-size;
        text-align: left;
      }

      a{
        padding: 10px 0;
        font-size: @font-size-big;
        line-height: 30px;
        text-align: left;

        span{
          font-size: @font-size-larger;
        }

        &.small-number{
          font-size: @contact-mob-font-size;
          line-height: @nav-font-size;

          span{
            font-size: @font-size-big;
          }
        }

      }

    }
  }

  });

}

#body-navigation{
  position: relative;
  background: #fff;
  .box-shadow(0 0 5px 0 rgba(0,0,0,.3));

  .navbar-header{
    border-top: 1px solid transparent;
    border-bottom: 1px solid transparent;
  }

  .container{
    padding: 0;
  }

  .sm({
    padding: 10px 0;

  .menu-header{
    .box-shadow(none);
  }
  });

  &.active-in{
    background: url("../../image/footer-bg.png") repeat;

    .menu-header{
      padding: 0;
    }
    .navbar-header{
      border-top: 1px solid @border-dark-nav-color;
      border-bottom: 1px solid @very-dark-color;
    }

    .btn-navbar{
      color: @lighten-color;
      padding-left: 25px;
    }

    .body-nav-links{
      border-top: 1px solid @border-dark-nav-color;
    }

    .body-nav{
      padding: 0 25px;

      li{
        display: block;
        width: 100%;
        float: none;
        border-top: 1px solid @border-dark-nav-color;
        border-bottom: 1px solid @very-dark-color;

        &:first-child{
          border: none;
        }

        a{
          padding: 20px 0;
          color: @main-color;
          text-align: center;

          &:hover{
            color: @lighten-color;
            background: transparent;
          }
        }

        &.dropdown:hover{
          .dropdown-menu{
            display: none;
          }

        }

      }
    }

  }

}

.body-nav-links{
  border-top: 1px solid transparent;
  border-bottom: 1px solid transparent;
}

.body-nav{
  .size-reset;
  .clearfix;

  li{
    display: block;
    float: left;
    list-style-type: none;

    a{
      display: block;
      padding: 10px 20px;
      font-size: @nav-font-size;
      line-height: @font-size-cill;
      color: @dark-color;
      border-radius: @border-radius-my;
      .bold;

      .sm-lg({
        padding: 10px 15px;
      });

      &:hover{
        color: @main-color;
        background: @lighten-color;
      }
    }
  }
}

.navbar-header{
  .btn-navbar{
    padding: 15px 40px 15px 10px;
    color: @dark-color;

    span{
      padding-left: 15px;
      font-size: @font-size-cill;
      line-height: @font-size-cill;
      text-transform: uppercase;
      .bold;
    }
  }

  .fa{
    font-size: 28px;
  }

}

#search{
  .form-control{
    display: block;
  }

  &.input-group{
    display: block;

    .form-control:first-child{
      border-bottom-right-radius: @border-radius-my;
      border-top-right-radius: @border-radius-my;
    }

  }

  .input-group-btn.search-inside{
    width: 30px;
    z-index: 111;
    position: absolute;
    top: 5px;
    right: 10px;

    .sm({
      right: 22%;
    });
    .md({
      right: 17%;
    });
    .lg({
      right: 13%;
    });

  }

  .input-group-btn:last-child>.btn{
    border-bottom-left-radius: @border-radius-my;
    border-top-left-radius: @border-radius-my;
  }
}

#menu{

  .dropdown:hover{

    .dropdown-menu{
      display: block;
    }

  }

  .menu-subcategory{
    .width(295);
    top: 100%;
    left: -10px;
    margin: 0;
    padding: 15px 20px 15px 10px;
    background: @main-color;
    border-top: none;
    border-bottom-left-radius: (@border-radius-my / 2);
    border-bottom-right-radius: (@border-radius-my / 2);
    .box-shadow(0 8px 7px -1px rgba(0,0,0,0.3));

    li{
      a{
        .width(265);
        padding: 10px 0 10px 30px;
        font-size: @contact-mob-font-size;
        color: @very-dark-color;
        .normal;
        text-align: left;

        &:hover{
          color: @main-color;
        }

      }
    }
  }
}

//cart
#cart{
  position: absolute;
  bottom: -67px;
  right: 30px;
  float: right;
  z-index: 1001;
  height: 70px;
  width: 70px;

  .btn-cart{
    width: 70px;
    height: 70px;
    .border-my(transparent);
    background: url(../../image/cart.png) no-repeat;
    border-radius: 50%;
    .box-shadow(5px 4px 7px -3px rgba(0,0,0,.3));

    &:hover,
    &:active,
    &:focus{
      .tab-focus;
    }

    #cart-total{
      display: block;
      position: absolute;
      top: 4px;
      right: -5px;
      padding: 0 7px;
      color: @lighten-color;
      font-size: @font-size-smaller;
      line-height: (@font-size-cill + 1);
      background: @main-color;
      border-radius: 50px;
      .box-shadow(1px 1px 4px 0px rgba(0,0,0,.3));
    }
  }

  .sm({
    position: static;
    height: 100%;
    width: 100%;

  .btn-cart{
    display: block;
    position: relative;
    padding: 74px;
    background-position: center right;
    float: right;
    border-radius: 0;
    .box-shadow(none);

    #cart-total{

      top: 40px;
      right: -5px;
      color: @lighten-color;
      font-size: @font-size-smaller;
      line-height: (@font-size-cill + 1);
      background: @main-color;
      border-radius: 50px;
      padding: 0 7px;
    }
  }
  });

}

.page-content{
  padding: 30px 0 0 0;
}

.main-page-pictures{
  .hidden-xs;
  .box-shadow-light;
  .round(12px);
  margin-bottom: 20px;

  img{
    .img-responsive;
  }
}

.category-module{
  margin-bottom: 30px;

  .list-group-item{
    margin: 0;
    padding: 15px 20px;
    font-size: @contact-mob-font-size;
    color: @dark-color;
    background: transparent;
    border: none;
    .big-border-radius;

    &:hover{
      color: @main-color;
      background: @lighten-color;
    }
  }
}

#sub-menu{
  .max-sm({
    margin-top: 30px;
  });
  a{
    display: block;
    padding: 30px 10px;
    font-size: @font-size-big;
    color: @dark-color;
    text-align: left;
    .bold;
    .clearfix;

    @media screen and (max-width:380px){
      font-size: @nav-font-size;
    }

    &:nth-of-type(1){
      padding-top: 0;
    }

    .sm({
    &:nth-of-type(1){
      padding-top: 40px;
    }
      padding: 20px 0;
      text-align: center;
    });

    .link-title{
      display: block;
      padding: 25px 0;

      @media screen and (min-width:380px){
        padding: 20px 0;
      }

      .sm({
        padding: 0;
      });
    }

    .sub-menu-img{
      display: block;
      float: left;
      margin: 0 30px 0 0;
      .round-shadow;
      .sizing(70,70);

      .sm({
        float: none;
        margin: 0 auto 20px auto;
        .sizing(150,150);
        .transition(all .25s ease-in-out);
      });

    }

    &.hookah{
      .sub-menu-img{
        .background-menu(@main-color, url("../../image/mob-sprite.png"), 15);
        .sm({
          .background-menu(@main-color, url("../../image/hookah-sprite.png"), 35);
        });
      }
    }

    &.accessories{
      .sub-menu-img{
        .background-menu(@main-color, url("../../image/mob-sprite.png"), -45);
        .sm({
          .background-menu(@main-color, url("../../image/acc-sprite.png"), 35);
        });
      }
    }

    &.tobacco{
      .sub-menu-img{
        .background-menu(@main-color, url("../../image/mob-sprite.png"), -122);
        .sm({
          .background-menu(@main-color, url("../../image/tobacco-sprite.png"), 45);
        });
      }
    }

    &.coal{
      .sub-menu-img{
        .background-menu(@main-color, url("../../image/mob-sprite.png"), -192);
        .sm({
          .background-menu(@main-color, url("../../image/coal-sprite.png"), 35);
        });
      }
    }

    &:hover{
      color: @lighten-color;

      .link-title{

        &:before{
          content: "•";
          margin-left: -20px;
          padding-right: 12px;
          color: @lighten-color;
        }

        .sm({
        &:before{
          content: "";
          margin: 0;
          padding: 0;
        }
        });
      }
    }

    .sm({
    &{
      position:relative;
    }

    &:hover{
      &:before{
        content: "•";
        display: block;
        position: absolute;
        bottom: 15%;
        left:0;
        right: 0;
        color: @lighten-color;
      }

      &.hookah{
        .sub-menu-img{
          .background-menu(@lighten-color, url("../../image/hookah-sprite.png"), -100);
        }
      }

      &.accessories{
        .sub-menu-img{
          .background-menu(@lighten-color, url("../../image/acc-sprite.png"), -90);
        }
      }

      &.tobacco{
        .sub-menu-img{
          .background-menu(@lighten-color, url("../../image/tobacco-sprite.png"), -75);
        }
      }
      &.coal{
        .sub-menu-img{
          .background-menu(@lighten-color, url("../../image/coal-sprite.png"), -105);
        }
      }

    }
    });
  }
}

.about-company{
  background: url(../../image/about-company-bg.jpg) 0 0 no-repeat;
  background-size: cover;
  padding-bottom: 160px;

  h2,
  p{
    color: @main-color;
  }
  h2{
    text-align: center;
  }

}


.homepage{
  .featured-module{
    h3{
      display: none;
    }
  }
}

.mobile-xs{
  .max-xs({
  .col-xs-6{
    width: 100%;
  }
  });

}

.my-module{
  padding-bottom: 20px;
}

.image-link{
  position: relative;
  display: block;
  .round(6px);
  border: 1px solid @border-image-color;

  img{
    width: 100%;
    margin: 0 auto;
    .round(6px);
  }
}

.bestseller,
.latest{
  img{
    max-width: 210px;
  }
}


.like-btn{
  position: absolute;
  bottom: 20px;
  left: 20px;
  margin: 0;
  padding: 8px 9px 4px 9px;
  color: @light-color;
  background: @main-color;
  .round;
  .box-shadow-light;
  .border-my(@main-color);
  .transition(all .15s ease-out);

  &:hover,
  &:active,
  &:focus{
    .tab-focus;
  }

  &:hover{
    color: @main-color;
    background: @light-color;
    .border-my(@light-color);
  }
}

.module-good{
  margin-bottom: 30px;
  .box-shadow-light;
  .round(12px);
  background: @main-color;

  .image{
    position: relative;
    padding: 20px 20px 0 20px;

    img{
      margin: 0 auto;
    }

  }

  .good-name{
    text-align: center;

    a{
      display: table-cell;
      padding: 10px 20px 0 20px;
      height: 110px;
      font-size: @contact-mob-font-size;
      line-height: @font-size-big;
      color: @dark-color;
      .bold;
      vertical-align: middle;

      .sm{
        height: 85px;
      }
    }
  }

  .caption{
    .basic-information{
      position: relative;
      width: 100%;
      min-height: 66px;
    }

    .price,
    .cart-button-block{
      position: absolute;
      top: 0;
    }


    .price{
      top: 5px;
      left: -5px;
      max-width: 174px;
      padding: 10px 20px;
      font-size: @nav-font-size;
      line-height: @contact-mob-font-size;
      color: @main-color;
      background: @light-color;
      border-bottom-right-radius: 36px;
      border-top-right-radius: 36px;

      &:before{
        content: "";
        position: absolute;
        bottom: -5px;
        left: 0;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 0 5px 5px 0;
        border-color: transparent @light-color-before transparent transparent;
      }

      &-faculty{
        top: -5px;

        span{
          display: block;

          &.price-old{
            padding-top: 2px;
            font-size: 14px;
            text-align: center;
            color: #666;
            text-decoration: line-through;
          }
        }

      }

    }
    .cart-button-block{
      right: -5px;
    }

  }

}

.cart-btn{
  color: @main-color;
  background: @lighten-color;
  font-size: @nav-font-size;
  padding: 9px 35px;
  .round(0);
  border-bottom-left-radius: 36px;
  border-top-left-radius: 36px;
  .box-shadow-light;

  &:hover{
    color: @main-color;
    background: lighten(@lighten-color, 10%);
  }
  &:focus{
    color: @main-color;
  }

  &:after{
    content: "";
    position: absolute;
    bottom: -5px;
    right: 0;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 5px 5px 0 0;
    border-color: @light-color-after transparent transparent transparent;
  }
}

.footer-module{
  padding: 30px 0;
  margin-bottom: 30px;
  border-top: 1px solid @border-module-color;
  text-align: center;
}

.bestseller,
.latest{
  margin-bottom: 30px;
  padding: 0;
  .round(12px);
  .box-shadow-light;
  background: @main-color;

  .title{
    padding: 20px 20px 20px;
  }

  .basic-information{
    overflow: hidden;
  }

  .module-good{
    .round(0);
    border-bottom-left-radius: 12px;
    border-bottom-right-radius: 12px;
    box-shadow: none;
    margin: 0;

    .caption{
      .price{
        background: @light-module-price-color;

        .sm({
          padding: 10px 10px;
        });
        .md({
          padding: 10px 20px;
        });

        &:before{
          border-color: transparent @light-module-price-color-before transparent transparent;
        }

      }

      .cart-btn{
        .sm({
          padding: 9px 25px;
        });
        .md({
          padding: 9px 35px;
        });

      }

      .price-old{
        display: none;
      }

    }
    .good-name{
      a{
        .sm({
          height: 130px;
        });
        .md({
          height: 105px;
        });
      }
    }
  }

}

.latest,
.bestseller,
.featured-module,
.category-page{
  .like-btn{
    left:40px;
  }
}

// footer

.homepage + footer{
  margin-top: 0;
}

footer{
  position: relative;
  background: url("../../image/footer-bg.png") repeat;
  margin-top: 50px;
}

.footer-logo{
  top: -50px;

  img{
    margin: 0 auto;
  }
}

.footer-information{
  font-size: @font-size-cill;
  color: @lighter-color;

  &.footer-adress{
    font-size: @font-size-big;
  }

  &.first-string{
    padding-bottom: 40px;
  }

  .sm({
    font-size: @font-size-big;
  });
}

.footer-contacts{
  margin: 0;
  padding: 0;

  li{
    margin: 0;
    padding: 0;
    text-align: center;

    &:after{
      .round-dot-footer;
    }

    &:last-child{
      &:after{
        content: "";
      }
    }

    .sm({
      &:after{
        content: "";
      }
    });


    a{
      display: block;
      padding: 20px 0;
      font-size: @font-size-big;
      line-height: @font-size-base;
      color: @lighter-color;
      .normal;

      .sm-lg({
        padding: 20px 0 50px;
        font-size: @nav-font-size;
      });

      span{
        font-size: @font-size-larger;
        .bold;

        .sm-lg({
          font-size: @font-size-big;
        })
      }

      &:hover{
        color: @lighten-color;
      }

      .md({
        position: relative;

      &:after{
        .round-dot-footer;
        position: absolute;
        top: 50%;
        right: 0;
        .translate(0,-50%);
      }

      });
    }

    .md({
    &:last-child{
      a{
        &:after{
          content: "";
        }
      }
    }
    })
  }
}

.social{
  padding: 50px 0 45px 0;
  text-align: center;

  ul{
    margin: 0 auto;
    padding: 0;
    .clearfix;

    li{
      position: relative;
      display: inline-block;
      margin: 0;
      padding: 0 15px;

      .sm({
        padding: 0 22px;
      });



      a{
        display: block;
        .round;
        .sizing(50,50);
        .border-my(@lighter-color);
        .transition(all .25s ease-in-out);
      }

      &:after{
        .round-dot-footer;
        position: absolute;
        top: 50%;
        right: 0;
        .translate(50%,-50%);
      }

      &:last-child{
        &:after{
          content: "";
        }
      }



      .fb{
        .background-soc(transparent, url(../../image/soc-sprite.png),18,12);

        &:hover{
          .background-soc(@lighten-color, url(../../image/soc-sprite.png),18,-26);
        }

      }

      .vk{
        .background-soc(transparent, url(../../image/soc-sprite.png),-22,13);

        &:hover{
          .background-soc(@lighten-color, url(../../image/soc-sprite.png),-22,-25);
        }

      }

      .gp{
        .background-soc(transparent, url(../../image/soc-sprite.png),-64, 12);

        &:hover{
          .background-soc(@lighten-color, url(../../image/soc-sprite.png),-64,-25);
        }

      }

    }
  }

}

.copyrights{
  border-top:1px solid @border-dark-nav-color;
  padding: 20px 15px;
}

#copy{
  text-align: center;
  color: @lighter-color;
  font-size: @font-size-base - 1;

  .sm({
    font-size: @font-size-base;
    text-align:left;
    padding-top: 10px;
  });

}

#reclamare{
  text-align: center;
  padding-top: 20px;

  &:before{
    content: '';
    width: 50px;
