.my-radio,
.my-checkbox{
  position: relative;
}

.my-radio{

  input[type="radio"]{
    position: absolute;
    margin-left: -99999px;
  }

  input[type="radio"] + label{
    cursor: pointer;

    &:before {
      content: "";
      display: inline-block;
      width: 16px;
      height: 16px;
      margin-right: 10px;
      margin-top: -7px;
      border: 1px solid #ccc;
      border-radius: 50%;
      background: #fff;
      vertical-align: middle;
      cursor: pointer;
    }

  }


  input[type=radio]:checked+label {
    &:after{
      content: "";
      position: absolute;
      top: 5px;
      left: 4px;
      width: 8px;
      height: 8px;
      border-radius: 50%;
      border: 1px solid @lighten-color;
      background: @lighten-color;
    }
  }
}


.my-checkbox{

  input[type="checkbox"]{
    position: absolute;
    margin-left: -99999px;
  }

  input[type="checkbox"] + label{
    cursor: pointer;

    &:before {
      content: "";
      display: inline-block;
      width: 16px;
      height: 16px;
      margin-right: 10px;
      margin-top: -7px;
      border: 1px solid #ccc;
      background: #fff;
      vertical-align: middle;
      cursor: pointer;
    }

  }


  input[type=checkbox]:checked+label {
    &:after{
      content: "";
      position: absolute;
      top: 15%;
      left: 1%;
      width: 16px;
      height: 16px;
      background: url("../../image/checbox-checked.png") 0 0 no-repeat;
    }
  }
}



