#offcanvas{
  display: block;
  position: fixed;
  width: 100vw;
  left: -100%;
  top: 0;
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
  z-index: 10000;
  -webkit-overflow-scrolling: touch;
  visibility: hidden;
  .transition(all 0.35s ease-in-out);

  .max-xs({
    max-width: 320px;
  });


  &.active{
    left: 0;
    visibility: visible;
    border-radius: 0;
  }
}

